//circle
let flag = true;
let flag2 = true;
let flag3 = true;
let flag4 = true;
let flag5 = true;
$(window).scroll(function() {
  if($(window).scrollTop() + $(window).height() >= $('#circle-1').offset().top && flag === true) {
    flag = false;
    circliful.newCircle({
      percent: 20,
      id: 'circle-1',
      type: 'simple',
      strokeGradient: ['#53BF51', '#53BF51'],
      backgroundCircleWidth: 5,
      foregroundCircleWidth: 5,
      animationStep: 1
    });
  } else if($(window).scrollTop() + $(window).height() >= $('#circle-2').offset().top && flag2 === true) {
    flag2 = false;
    circliful.newCircle({
      percent: 40,
      id: 'circle-2',
      type: 'simple',
      strokeGradient: ['#53BF51', '#53BF51'],
      backgroundCircleWidth: 5,
      foregroundCircleWidth: 5,
      animationStep: 1.5
    });
  } else if($(window).scrollTop() + $(window).height() >= $('#circle-3').offset().top && flag3 === true) {
    flag3 = false;
    circliful.newCircle({
      percent: 60,
      id: 'circle-3',
      type: 'simple',
      strokeGradient: ['#53BF51', '#53BF51'],
      backgroundCircleWidth: 5,
      foregroundCircleWidth: 5,
      animationStep: 2
    });
  } else if($(window).scrollTop() + $(window).height() >= $('#circle-4').offset().top && flag4 === true) {
    flag4 = false;
    circliful.newCircle({
      percent: 80,
      id: 'circle-4',
      type: 'simple',
      strokeGradient: ['#53BF51', '#53BF51'],
      backgroundCircleWidth: 5,
      foregroundCircleWidth: 5,
      animationStep: 2.5
    });
  } else if($(window).scrollTop() + $(window).height() >= $('#circle-5').offset().top && flag5 === true) {
    flag5 = false;
    circliful.newCircle({
      percent: 100,
      id: 'circle-5',
      type: 'simple',
      strokeGradient: ['#53BF51', '#53BF51'],
      backgroundCircleWidth: 5,
      foregroundCircleWidth: 5,
      animationStep: 3
    });
  }
});
